// Robust year extractor for date strings; falls back to regex if Date parsing fails
export const extractYear = (value) => {
  if (!value) return null;
  const d = new Date(value);
  if (!Number.isNaN(d.getTime())) return d.getFullYear();
  const match = String(value).match(/(\d{4})/);
  return match ? Number(match[1]) : null;
};

export const norm = (v) => (v === null || v === undefined ? null : String(v).trim());

export const eqi = (a, b) => {
  if (a === null || a === undefined || b === null || b === undefined) return false;
  return String(a).trim().toLowerCase() === String(b).trim().toLowerCase();
};

/**
 * Transform rows to PO-level data
 * Groups filtered rows by PO and returns chart-ready data + risk summary
 */
export function transformRowsToPOLevel(filteredRows) {
  const filtered = filteredRows.filter((r) => {
    const yr = extractYear(r.po_creation_date);
    if (yr !== null && yr > 2025) return false;
    return true;
  });
  const groups = new Map();
  filtered.forEach((r, idx) => {
    const key = r.po_number || `po-${idx}`;
    if (!groups.has(key)) {
      groups.set(key, {
        po_number: key,
        po_creation_date: r.po_creation_date || null,
        total_cost: 0,
        lead_time_days_sum: 0,
        lead_time_days_count: 0,
        defect_quantity: 0,
        produced_quantity: 0,
        good_pieces: 0,
        oee_pct_sum: 0,
        oee_pct_count: 0,
        risk_score:
          r.risk_score !== null && r.risk_score !== undefined
            ? Number(r.risk_score)
            : null,
        risk_level: r.risk_level || null,
      });
    }
    const g = groups.get(key);
    const cost = Number(r.total_cost);
    if (!Number.isNaN(cost)) g.total_cost += cost;
    const ltd = Number(r.lead_time_days);
    if (!Number.isNaN(ltd)) {
      g.lead_time_days_sum += ltd;
      g.lead_time_days_count += 1;
    }
    const defect = Number(r.defect_quantity);
    if (!Number.isNaN(defect)) g.defect_quantity += defect;
    const produced = Number(r.produced_quantity);
    if (!Number.isNaN(produced)) g.produced_quantity += produced;
    const good = Number(r.good_pieces);
    if (!Number.isNaN(good)) g.good_pieces += good;
    const oee = Number(r.oee_pct);
    if (!Number.isNaN(oee)) {
      g.oee_pct_sum += oee;
      g.oee_pct_count += 1;
    }
    // Keep highest risk_score and its level if multiple rows
    const rowRisk =
      r.risk_score !== null && r.risk_score !== undefined
        ? Number(r.risk_score)
        : null;
    if (rowRisk !== null && (g.risk_score === null || rowRisk > g.risk_score)) {
      g.risk_score = rowRisk;
      g.risk_level = r.risk_level || g.risk_level;
    }
  });
  const poLevelData = Array.from(groups.values())
    .map((g) => ({
      po_number: g.po_number,
      po_creation_date: g.po_creation_date,
      total_cost: g.total_cost,
      lead_time_days:
        g.lead_time_days_count > 0
          ? g.lead_time_days_sum / g.lead_time_days_count
          : null,
      defect_quantity: g.defect_quantity,
      produced_quantity: g.produced_quantity,
      good_pieces: g.good_pieces,
      oee_pct: g.oee_pct_count > 0 ? g.oee_pct_sum / g.oee_pct_count : null,
      risk_score: g.risk_score,
      risk_level: g.risk_level,
    }))
    // Exclude future/irrelevant year 2026+ from the chart (based on po_creation_date)
    .filter((g) => {
      const yr = extractYear(g.po_creation_date);
      if (yr === null) return true;
      return yr <= 2025;
    })
    .sort((a, b) => {
      const da = a.po_creation_date
        ? new Date(a.po_creation_date).getTime()
        : Infinity;
      const db = b.po_creation_date
        ? new Date(b.po_creation_date).getTime()
        : Infinity;
      return da - db;
    });
  // max risk (used only for color)
  let riskScore = null;
  let riskLevel = "No Data";
  if (poLevelData.length) {
    poLevelData.forEach((p) => {
      if (p.risk_score !== null && p.risk_score !== undefined) {
        if (riskScore === null || p.risk_score > riskScore) {
          riskScore = p.risk_score;
          riskLevel = p.risk_level || "No Data";
        }
      }
    });
    if (riskScore === null) riskLevel = "No Data";
  }
  return { poLevelData, riskSummary: { riskScore, riskLevel } };
}

/**
 * Summarize risk distribution from PO-level data
 */
export function summarizeRisk(poLines) {
  if (!poLines || poLines.length === 0) {
    return {
      n_pos: 0,
      safe_count: 0,
      medium_count: 0,
      high_count: 0,
      high_pct: 0,
      avg_risk_score: null,
      overall_level: "No Data",
    };
  }
  const n_pos = poLines.length;
  let safe = 0;
  let med = 0;
  let high = 0;
  let scoreSum = 0;
  let scoreCount = 0;
  poLines.forEach((p) => {
    if (p.risk_level === "Safe") safe += 1;
    else if (p.risk_level === "Medium Risk") med += 1;
    else if (p.risk_level === "High Risk") high += 1;
    if (
      p.risk_score !== null &&
      p.risk_score !== undefined &&
      !Number.isNaN(p.risk_score)
    ) {
      scoreSum += Number(p.risk_score);
      scoreCount += 1;
    }
  });
  const high_pct = n_pos > 0 ? high / n_pos : 0;
  const avg_risk_score = scoreCount > 0 ? scoreSum / scoreCount : null;
  let overall_level = "Need More Data";
  if (n_pos < 3) {
    overall_level = "Need More Data";
  } else if (high_pct >= 0.5 && (avg_risk_score ?? 0) >= 10) {
    overall_level = "High Risk";
  } else if (high_pct >= 0.2 || (avg_risk_score ?? 0) >= 6) {
    overall_level = "Medium Risk";
  } else {
    overall_level = "Safe";
  }
  return {
    n_pos,
    safe_count: safe,
    medium_count: med,
    high_count: high,
    high_pct,
    avg_risk_score,
    overall_level,
  };
}

// Simple city-level overrides (optional refinement)
const CITY_COORDS = {
  "dallas, tx": [-96.797, 32.7767],
  "tokyo, jp": [139.6917, 35.6895],
  "singapore": [103.8198, 1.3521],
  "mumbai, in": [72.8777, 19.076],
  "shanghai, cn": [121.4737, 31.2304],
  "berlin, de": [13.405, 52.52],
  "paris, fr": [2.3522, 48.8566],
  "london, uk": [-0.1276, 51.5074],
  "chicago, il": [-87.6298, 41.8781],
  "los angeles, ca": [-118.2437, 34.0522],
};

// Heuristic mapping from region/location  [lon, lat]
export function getCoords(region, location) {
  const loc = location ? String(location).trim().toLowerCase() : "";
  if (loc && CITY_COORDS[loc]) {
    return CITY_COORDS[loc];
  }
  const regionStr = region ? String(region).trim().toLowerCase() : "";
  // Region-based fallbacks
  if (
    regionStr.includes("north america") ||
    regionStr === "na" ||
    regionStr.includes("united states") ||
    regionStr.includes("usa")
  ) {
    return [-98, 39]; // US centroid-ish
  }
  if (
    regionStr.includes("emea") ||
    regionStr.includes("europe") ||
    regionStr.includes("middle east") ||
    regionStr.includes("africa")
  ) {
    return [10, 50];
  }
  if (
    regionStr.includes("apac") ||
    regionStr.includes("asia") ||
    regionStr.includes("pacific")
  ) {
    return [105, 15];
  }
  // Location-only fallbacks if region is missing/unclear
  if (loc.includes("usa") || loc.includes("united states") || loc.includes("tx")) {
    return [-98, 39];
  }
  if (loc.includes("germany") || loc.includes("france") || loc.includes("uk")) {
    return [10, 50];
  }
  if (
    loc.includes("india") ||
    loc.includes("china") ||
    loc.includes("japan") ||
    loc.includes("singapore")
  ) {
    return [105, 15];
  }
  return null;
}

/**
 * Build bubble map data aggregated by physical location & role
 */
export function buildBubbleMapData(filteredRows) {
  if (!filteredRows || !filteredRows.length) return [];
  const nodeMap = new Map();
  const ensureNode = (role, id, name, region, location, row) => {
    if (!id || !location) return;
    if (!nodeMap.has(id)) {
      nodeMap.set(id, {
        id,
        role, // "customer" | "supplier" | "plant"
        name: name || location || region || id,
        region: region || null,
        location: location || null,
        poSet: new Set(),
        total_cost_sum: 0,
        lead_time_sum: 0,
        lead_time_count: 0,
        defect_qty_sum: 0,
        oee_sum: 0,
        oee_count: 0,
        risk_sum: 0,
        risk_count: 0,
        // On-time delivery tracking
        on_time_count: 0,
        on_time_total: 0,
        // Delayed days tracking (for when on-time is 0%)
        delayed_days_sum: 0,
        delayed_days_count: 0,
      });
    }
    const n = nodeMap.get(id);
    if (row.po_number) n.poSet.add(row.po_number);
    const cost = Number(row.total_cost);
    if (!Number.isNaN(cost)) n.total_cost_sum += cost;
    const ltd = Number(row.lead_time_days);
    if (!Number.isNaN(ltd)) {
      n.lead_time_sum += ltd;
      n.lead_time_count += 1;
    }
    const defect = Number(row.defect_quantity);
    if (!Number.isNaN(defect)) n.defect_qty_sum += defect;
    const oee = Number(row.oee_pct);
    if (!Number.isNaN(oee)) {
      n.oee_sum += oee;
      n.oee_count += 1;
    }
    const rs =
      row.risk_score !== null && row.risk_score !== undefined
        ? Number(row.risk_score)
        : NaN;
    if (!Number.isNaN(rs)) {
      n.risk_sum += rs;
      n.risk_count += 1;
    }

    // On-time delivery tracking - calculate from actual vs planned delivery dates
    const actualDateStr = row.actual_delivery_date;
    const plannedDateStr = row.planned_delivery_date;
    
    // Check if both dates exist and are not empty strings
    if (
      actualDateStr != null &&
      actualDateStr !== "" &&
      plannedDateStr != null &&
      plannedDateStr !== ""
    ) {
      const actualDate = new Date(actualDateStr);
      const plannedDate = new Date(plannedDateStr);
      
      // Check if both dates are valid (not NaN)
      if (
        !Number.isNaN(actualDate.getTime()) &&
        !Number.isNaN(plannedDate.getTime())
      ) {
        n.on_time_total += 1;
        // Compare dates at day level (ignore time)
        // Normalize to midnight for accurate day comparison
        const actualDay = new Date(actualDate.getFullYear(), actualDate.getMonth(), actualDate.getDate());
        const plannedDay = new Date(plannedDate.getFullYear(), plannedDate.getMonth(), plannedDate.getDate());
        
        // On-time if actual <= planned (delivered on or before planned date)
        if (actualDay.getTime() <= plannedDay.getTime()) {
          n.on_time_count += 1;
        } else {
          // Track delayed days for late deliveries
          const delayedMs = actualDay.getTime() - plannedDay.getTime();
          const delayedDays = Math.floor(delayedMs / (1000 * 60 * 60 * 24));
          n.delayed_days_sum += delayedDays;
          n.delayed_days_count += 1;
        }
      }
    }
  };
  filteredRows.forEach((r) => {
    // Customer node
    const custLoc = r.customer_location || null;
    const custRegion = r.customer_region || null;
    const custName = r.customer_name || custLoc;
    if (custLoc) {
      const id = `customer:${custLoc}`;
      ensureNode("customer", id, custName, custRegion, custLoc, r);
    }
    // Supplier node
    const suppLoc = r.supplier_location || null;
    const suppRegion = r.supplier_region || null;
    const suppName = r.supplier_name || suppLoc;
    if (suppLoc) {
      const id = `supplier:${suppLoc}`;
      ensureNode("supplier", id, suppName, suppRegion, suppLoc, r);
    }
    // Plant node
    const plantLoc = r.plant_location || r.plant_id || null;
    const plantRegion = r.plant_region || null;
    const plantName = r.plant_id || plantLoc;
    if (plantLoc) {
      const id = `plant:${plantLoc}`;
      ensureNode("plant", id, plantName, plantRegion, plantLoc, r);
    }
  });
  const nodes = [];
  nodeMap.forEach((n) => {
    const coords = getCoords(n.region, n.location);
    if (!coords) return; // skip nodes without usable coordinates
    const po_count = n.poSet.size;
    const avg_lead_time_days =
      n.lead_time_count > 0 ? n.lead_time_sum / n.lead_time_count : null;
    const avg_oee_pct = n.oee_count > 0 ? n.oee_sum / n.oee_count : null;
    const avg_risk_score =
      n.risk_count > 0 ? n.risk_sum / n.risk_count : null;
    let risk_bucket = "No Data";
    if (avg_risk_score !== null && !Number.isNaN(avg_risk_score)) {
      if (avg_risk_score >= 11) risk_bucket = "High";
      else if (avg_risk_score >= 5) risk_bucket = "Medium";
      else if (avg_risk_score < 5) risk_bucket = "Safe";
    }

    // Calculate on-time delivery percentage
    const on_time_delivery_pct =
      n.on_time_total > 0 ? (n.on_time_count / n.on_time_total) * 100 : null;

    // Calculate average delayed days (for when on-time delivery is 0%)
    const avg_delayed_days =
      n.delayed_days_count > 0 ? n.delayed_days_sum / n.delayed_days_count : null;

    nodes.push({
      id: n.id,
      role: n.role,
      name: n.name,
      region: n.region,
      location: n.location,
      coords,
      po_count,
      total_cost_sum: n.total_cost_sum,
      avg_lead_time_days,
      defect_qty_sum: n.defect_qty_sum,
      avg_oee_pct,
      avg_risk_score,
      risk_bucket,
      on_time_delivery_pct,
      avg_delayed_days,
    });
  });
  return nodes;
}

/**
 * Process Integrated data and calculate all metrics
 * @param {Object} params - Processing parameters
 * @param {Array} params.rows - Raw data rows from Supabase
 * @param {Object} params.filters - Filter object with customer, part, supplier, year
 * @param {boolean} params.dataLoading - Whether data is currently loading
 * @param {boolean} params.dataError - Whether there was an error loading data
 * @param {string[]} params.missingColumns - Array of missing column names
 * @returns {Object} Processed data including filter options, KPIs, and chart data
 */
export function processIntegratedData({
  rows,
  filters,
  dataLoading,
  dataError,
  missingColumns = [],
}) {
  /**
   * Helper function to check if any of the required columns are missing
   * @param {string[]} requiredColumns - Array of column names required for this component
   * @returns {boolean} True if any required column is missing
   */
  const hasMissingDependency = (requiredColumns) => {
    return requiredColumns.some(col => missingColumns.includes(col));
  };
  const customerOptions = Array.from(
    new Set(rows.map((r) => norm(r.customer_name)).filter(Boolean))
  ).sort((a, b) => a.localeCompare(b));
  
  const customerFiltered = !filters.customer
    ? rows
    : rows.filter((r) => eqi(r.customer_name, filters.customer));
  
  const partOptions = Array.from(
    new Set(customerFiltered.map((r) => norm(r.part_name)).filter(Boolean))
  ).sort((a, b) => a.localeCompare(b));
  
  let supplierSource = customerFiltered;
  if (filters.part !== "All") {
    supplierSource = supplierSource.filter((r) => eqi(r.part_name, filters.part));
  }
  
  const supplierOptions = Array.from(
    new Set(
      supplierSource.map((r) => norm(r.supplier_name)).filter(Boolean)
    )
  ).sort((a, b) => a.localeCompare(b));

  // Extract available years from po_creation_date
  const years = Array.from(
    new Set(
      rows
        .map((r) => extractYear(r.po_creation_date))
        .filter((yr) => yr !== null && yr <= 2025)
    )
  ).sort((a, b) => b - a); // Sort descending (newest first)
  const yearOptions = years.map((yr) => String(yr));
  
  // Apply filters: customer required, part/supplier/year optional ("All")
  const filteredRows = rows.filter((r) => {
    if (!filters.customer) return false;
    if (!eqi(r.customer_name, filters.customer)) return false;
    if (filters.part !== "All" && !eqi(r.part_name, filters.part)) return false;
    if (filters.supplier !== "All" && !eqi(r.supplier_name, filters.supplier))
      return false;
    // Apply year filter
    if (filters.year !== "All") {
      const rowYear = extractYear(r.po_creation_date);
      if (rowYear === null || String(rowYear) !== filters.year) return false;
    }
    return true;
  });
  
  const { poLevelData, riskSummary } = transformRowsToPOLevel(filteredRows);
  const bubbleNodes = buildBubbleMapData(filteredRows);
  
  // Calculate On-Time Delivery % from filteredRows
  let onTimeCount = 0;
  let onTimeTotal = 0;
  filteredRows.forEach((row) => {
    const actualDateStr = row.actual_delivery_date;
    const plannedDateStr = row.planned_delivery_date;
    if (actualDateStr && plannedDateStr) {
      try {
        const actualDate = new Date(actualDateStr);
        const plannedDate = new Date(plannedDateStr);
        if (!Number.isNaN(actualDate.getTime()) && !Number.isNaN(plannedDate.getTime())) {
          // Normalize to midnight (year, month, day only)
          const actualNormalized = new Date(actualDate.getFullYear(), actualDate.getMonth(), actualDate.getDate());
          const plannedNormalized = new Date(plannedDate.getFullYear(), plannedDate.getMonth(), plannedDate.getDate());
          onTimeTotal += 1;
          if (actualNormalized.getTime() <= plannedNormalized.getTime()) {
            onTimeCount += 1;
          }
        }
      } catch (e) {
        // Skip invalid dates
      }
    }
  });
  const onTimeDeliveryPct = onTimeTotal > 0 ? (onTimeCount / onTimeTotal) * 100 : null;
  
  // Calculate Average Lead Time from poLevelData
  const leadTimes = poLevelData
    .map((p) => p.lead_time_days)
    .filter((lt) => lt !== null && lt !== undefined && !Number.isNaN(lt));
  const avgLeadTime = leadTimes.length > 0
    ? leadTimes.reduce((sum, lt) => sum + lt, 0) / leadTimes.length
    : null;

  return {
    // Filter options (depend on customer_name, part_name, supplier_name, po_creation_date)
    customerOptions: hasMissingDependency(["customer_name"]) ? [] : customerOptions,
    partOptions: hasMissingDependency(["part_name"]) ? [] : partOptions,
    supplierOptions: hasMissingDependency(["supplier_name"]) ? [] : supplierOptions,
    yearOptions: hasMissingDependency(["po_creation_date"]) ? [] : yearOptions,
    filteredCount: filteredRows.length,
    totalLoadedCount: rows.length,
    // PO-level data (depends on po_number, po_creation_date, total_cost, lead_time_days, defect_quantity, produced_quantity, good_pieces, oee_pct, risk_score, risk_level)
    poLevelData: hasMissingDependency(["po_number", "po_creation_date", "total_cost", "lead_time_days", "defect_quantity", "produced_quantity", "good_pieces", "oee_pct", "risk_score", "risk_level"])
      ? []
      : poLevelData,
    // Risk summary (depends on risk_score, risk_level)
    riskSummary: hasMissingDependency(["risk_score", "risk_level"])
      ? { riskLevel: "Unknown", riskScore: null, poCount: 0 }
      : riskSummary,
    // Bubble map nodes (depends on customer_location, supplier_location, plant_location, customer_region, supplier_region, plant_region, customer_name, supplier_name, plant_id)
    bubbleNodes: hasMissingDependency(["customer_location", "supplier_location", "plant_location", "customer_region", "supplier_region", "plant_region", "customer_name", "supplier_name", "plant_id"])
      ? []
      : bubbleNodes,
    // On-time delivery (depends on actual_delivery_date, planned_delivery_date)
    onTimeDeliveryPct: hasMissingDependency(["actual_delivery_date", "planned_delivery_date"])
      ? null
      : onTimeDeliveryPct,
    // Average lead time (depends on lead_time_days from poLevelData)
    avgLeadTime: hasMissingDependency(["lead_time_days"])
      ? null
      : avgLeadTime,
  };
}

/**
 * Prepare chart data from PO-level data with optional monthly aggregation
 */
export function prepareChartData(poLevelData, filters) {
  // base: PO-level, capped at 2025
  const base = poLevelData.filter((p) => {
    const yr = extractYear(p.po_creation_date);
    return yr !== null && yr <= 2025;
  });
  const aggregateByMonth =
    filters.part === "All" && filters.supplier === "All";
  if (!aggregateByMonth) return base;
  // bucket by month when part/vendor is "All"
  const buckets = new Map();
  base.forEach((p) => {
    if (!p.po_creation_date) return;
    const d = new Date(p.po_creation_date);
    if (Number.isNaN(d.getTime())) return;
    const key = `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(
      2,
      "0"
    )}`;
    if (!buckets.has(key)) {
      buckets.set(key, {
        po_creation_date: `${key}-01`,
        total_cost: 0,
        lead_time_days_sum: 0,
        lead_time_days_count: 0,
        defect_quantity: 0,
        oee_sum: 0,
        oee_count: 0,
      });
    }
    const b = buckets.get(key);
    b.total_cost += Number(p.total_cost) || 0;
    if (
      p.lead_time_days !== null &&
      p.lead_time_days !== undefined &&
      !Number.isNaN(p.lead_time_days)
    ) {
      b.lead_time_days_sum += Number(p.lead_time_days);
      b.lead_time_days_count += 1;
    }
    b.defect_quantity += Number(p.defect_quantity) || 0;
    if (
      p.oee_pct !== null &&
      p.oee_pct !== undefined &&
      !Number.isNaN(p.oee_pct)
    ) {
      b.oee_sum += Number(p.oee_pct);
      b.oee_count += 1;
    }
  });
  return Array.from(buckets.values())
    .map((b) => ({
      po_creation_date: b.po_creation_date,
      total_cost: b.total_cost,
      lead_time_days:
        b.lead_time_days_count > 0
          ? b.lead_time_days_sum / b.lead_time_days_count
          : null,
      defect_quantity: b.defect_quantity,
      oee_pct: b.oee_count > 0 ? b.oee_sum / b.oee_count : null,
    }))
    .sort(
      (a, b) => new Date(a.po_creation_date) - new Date(b.po_creation_date)
    );
}



