import React from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
  LabelList,
} from "recharts";

/**
 * Revenue per Marketing Dollar Gauge
 */
export function RevenueGauge({ revenueMultiple, dataLoading, dataError }) {
  return (
    <div className="mt-8 bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-5 shadow-lg backdrop-blur-sm">
      <div className="flex items-center justify-between flex-wrap gap-3">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-sm font-semibold text-white">
              Revenue per Marketing Dollar
            </h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Measures marketing efficiency by showing how many dollars of revenue are generated for each dollar spent on marketing. Higher values indicate better return on marketing investment.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/70">
            Shows how many dollars of booked revenue you're earning for each
            dollar of marketing spend with these filters.
          </p>
        </div>
        {/* Numeric display for revenue multiple */}
        <div className="text-2xl font-bold text-white">
          {dataLoading ||
          dataError ||
          revenueMultiple === null ||
          Number.isNaN(revenueMultiple)
            ? ""
            : `${revenueMultiple.toFixed(1)}`}
        </div>
      </div>

      {/* Gauge-like bar */}
      <div className="mt-4">
        <div className="w-full h-3 rounded-full bg-emerald-900/60 border border-emerald-500/40 overflow-hidden">
          <div
            className="h-full bg-gradient-to-r from-emerald-400 via-emerald-300 to-emerald-200"
            style={{
              width:
                dataLoading ||
                dataError ||
                revenueMultiple === null ||
                Number.isNaN(revenueMultiple)
                  ? "0%"
                  : `${Math.min((revenueMultiple / 25) * 100, 100)}%`,
              transition: "width 0.4s ease, background 0.4s ease",
            }}
          />
        </div>
        {/* Gauge labels */}
        <div className="mt-2 flex justify-between text-[11px] text-emerald-100/70">
          <span>0</span>
          <span>25 (capped)</span>
        </div>
      </div>
    </div>
  );
}

/**
 * Funnel Progression Visualization
 */
export function FunnelProgression({ stageCounts, dataLoading, dataError }) {
  if (!stageCounts) return null;

  // Stages in funnel order
  const stages = [
    {
      id: "generated",
      label: "Leads Generated",
      value: stageCounts.generated,
    },
    {
      id: "converted",
      label: "Leads Converted",
      value: stageCounts.converted,
    },
    {
      id: "opps",
      label: "Opportunities",
      value: stageCounts.opportunities,
    },
    {
      id: "customers",
      label: "Customers",
      value: stageCounts.customers,
    },
  ];

  // Colors used for each stage bar
  const colors = ["#16a34a", "#2dd4bf", "#0ea5e9", "#6366f1"];
  // Top stage value used for relative width scaling
  const topValue = Math.max(
    stages[0] && Number.isFinite(stages[0].value) ? stages[0].value : 0,
    1
  );

  return (
    <div className="bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-5 shadow-lg backdrop-blur-sm">
      <div className="flex items-center justify-between flex-wrap gap-3 mb-4">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-sm font-semibold text-white">
              Funnel Progression
            </h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Visualizes the customer journey from initial leads through conversions, opportunities, and final customers. Shows how many leads progress through each stage and the conversion rates between stages.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/70">
            Leads flowing to conversions, opportunities, and customers
            with current filters.
          </p>
        </div>
      </div>

      {/* Custom funnel style using divs with clipPath */}
      <div className="flex flex-col items-center gap-3">
        {stages.map((stage, idx) => {
          // Previous stage value for percentage calculation
          const prevVal =
            idx === 0 || !Number.isFinite(stages[idx - 1].value)
              ? null
              : stages[idx - 1].value;

          // Conversion ratio from previous stage to current
          const ratio =
            prevVal &&
            prevVal > 0 &&
            Number.isFinite(stage.value)
              ? (stage.value / prevVal) * 100
              : null;

          // Width percentage of bar based on relative value
          const widthPct =
            dataLoading ||
            dataError ||
            !Number.isFinite(stage.value)
              ? 0
              : Math.max(
                  18, // minimum visible width
                  Math.min((stage.value / topValue) * 100, 100)
                );

          // Color picked from array
          const color = colors[idx % colors.length];

          // Calculate minimum width needed based on text length
          const prevStageLabel = idx > 0 ? stages[idx - 1].label : "";
          const percentageText =
            idx === 0
              ? "Starting volume"
              : ratio !== null && !Number.isNaN(ratio)
              ? `${ratio.toFixed(1)}% of ${prevStageLabel}`
              : "";
          const estimatedTextWidth = percentageText.length * 7;
          const minWidthNeeded = Math.max(200, estimatedTextWidth + 60);

          return (
            <div
              key={stage.id}
              className="relative text-center text-white font-semibold"
              style={{ width: `${widthPct}%`, minWidth: `${minWidthNeeded}px` }}
            >
              <div
                className="mx-auto py-4 rounded-md px-4"
                style={{
                  backgroundColor: color,
                  clipPath:
                    idx === stages.length - 1
                      ? "polygon(0 0, 100% 0, 100% 100%, 0 100%)"
                      : "polygon(0 0, 100% 0, 85% 100%, 15% 100%)",
                }}
              >
                {/* Stage value */}
                <div className="text-lg">
                  {dataLoading
                    ? "Loading..."
                    : dataError
                    ? ""
                    : stage.value.toLocaleString()}
                </div>
                {/* Stage label */}
                <div className="text-xs font-normal mt-1 text-white/90">
                  {stage.label}
                </div>
                {/* Conversion from prior stage */}
                <div className="text-[11px] font-normal mt-1 text-white/80 break-words whitespace-normal leading-tight">
                  {idx === 0
                    ? "Starting volume"
                    : dataLoading ||
                      dataError ||
                      ratio === null ||
                      Number.isNaN(ratio)
                    ? ""
                    : (() => {
                        const prevStageLabel = idx > 0 ? stages[idx - 1].label : "";
                        return `${ratio.toFixed(1)}% of ${prevStageLabel}`;
                      })()}
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

/**
 * Top 5 Channels by Conversion Rate Bar Chart
 */
export function Top5ChannelsChart({ top5Channels, dataLoading, dataError }) {
  return (
    <div className="bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-7 shadow-lg backdrop-blur-sm">
      <div className="flex items-center justify-between flex-wrap gap-3 mb-4">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-sm font-semibold text-white">
              Top 5 Channels by Conversion Rate
            </h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Shows the top 5 marketing channels ranked by their conversion rate (percentage of leads that become customers). Helps identify which channels are most effective at converting leads into customers.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/70 max-w-xs">
            Lead-to-customer conversion rate by marketing channel with the
            current filters applied.
          </p>
        </div>
      </div>

      {/* Chart state handling: loading / error / no data / chart */}
      {dataLoading ? (
        <div className="text-xs text-emerald-100/70">Loading</div>
      ) : dataError ? (
        <div className="text-xs text-red-200">
          Unable to compute channel conversion.
        </div>
      ) : !top5Channels.length ? (
        <div className="text-xs text-emerald-100/70">
          No channels with sufficient leads for this filter selection.
        </div>
      ) : (
        <div className="w-full" style={{ height: 360 }}>
          <ResponsiveContainer width="100%" height="100%">
            <BarChart
              data={top5Channels}
              margin={{ top: 10, right: 20, left: 60, bottom: 80 }}
            >
              <CartesianGrid
                strokeDasharray="3 3"
                stroke="rgba(16, 185, 129, 0.18)"
                vertical={false}
              />
              <XAxis
                dataKey="channel"
                interval={0}
                angle={-25}
                textAnchor="end"
                height={50}
                tick={{ fontSize: 11, fill: "#A7F3D0" }}
              />
              <YAxis
                tickFormatter={(v) => `${v}%`}
                tick={{ fontSize: 11, fill: "#A7F3D0" }}
                width={60}
                label={{
                  value: "Conversion Rate (%)",
                  angle: -90,
                  position: "left",
                  offset: 10,
                  fill: "#A7F3D0",
                  fontSize: 11,
                  style: { textAnchor: "middle" },
                }}
              />
              <Tooltip
                cursor={{ fill: "rgba(16, 185, 129, 0.12)" }}
                contentStyle={{
                  backgroundColor: "#022c22",
                  border: "1px solid rgba(16,185,129,0.4)",
                  borderRadius: "0.75rem",
                  fontSize: "11px",
                  color: "#ECFDF5",
                }}
                formatter={(value, name) =>
                  name === "conversionPct"
                    ? [`${value.toFixed(1)}%`, "Conversion Rate"]
                    : [value, name]
                }
                labelFormatter={(label, payload) => {
                  if (!payload || !payload.length) return label;
                  const d = payload[0].payload;
                  return `${d.channel}  ${d.customers.toLocaleString()} customers from ${d.leads.toLocaleString()} leads`;
                }}
              />
              <Bar
                dataKey="conversionPct"
                fill="#34D399"
                radius={[4, 4, 0, 0]}
              >
                <LabelList
                  dataKey="conversionPct"
                  position="top"
                  formatter={(v) => `${v.toFixed(1)}%`}
                  fill="#ECFDF5"
                  fontSize={11}
                />
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}
    </div>
  );
}

/**
 * Sales Cycle Duration Summary
 */
export function SalesCycleDuration({ salesCycle, dataLoading, dataError }) {
  return (
    <div className="bg-gradient-to-r from-emerald-900/70 via-emerald-800/60 to-emerald-900/70 border border-emerald-500/40 rounded-3xl p-6 shadow-lg backdrop-blur-sm">
      <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-lg font-semibold text-white">
              Sales Cycle Duration
            </h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Average number of days it takes from when a marketing campaign starts to when a customer is created. Shorter cycles indicate faster conversion and more efficient sales processes.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/80">
            Avg days from campaign start to customer creation (filter-aware).
          </p>
          {/* Year-wise breakdown */}
          {!dataLoading && !dataError && salesCycle.byYear.size > 0 && (
            <div className="mt-3 grid grid-cols-2 gap-3 text-[12px] text-emerald-100/80">
              {Array.from(salesCycle.byYear.entries())
                .map(([year, v]) => ({
                  year: String(year),
                  avg: v.count > 0 ? v.sum / v.count : 0,
                }))
                .sort((a, b) => {
                  const na = Number(a.year);
                  const nb = Number(b.year);
                  if (!Number.isNaN(na) && !Number.isNaN(nb)) return na - nb;
                  return a.year.localeCompare(b.year);
                })
                .map((item) => (
                  <div
                    key={item.year}
                    className="rounded-xl border border-emerald-500/30 bg-black/10 px-3 py-2"
                  >
                    <div className="text-[11px] text-emerald-100/70">
                      {item.year}
                    </div>
                    <div className="text-sm font-semibold text-white">
                      {item.avg.toFixed(1)} days
                    </div>
                  </div>
                ))}
            </div>
          )}
        </div>
        {/* Overall sales cycle average */}
        <div className="min-w-[180px] text-right">
          <div className="text-xs uppercase tracking-wide text-emerald-200/80">
            Overall avg
          </div>
          <div className="text-3xl font-bold text-white mt-1">
            {dataLoading ||
            dataError ||
            salesCycle.overallCount === 0
              ? ""
              : `${(salesCycle.overallSum / salesCycle.overallCount).toFixed(1)}d`}
          </div>
        </div>
      </div>
      {/* State messages */}
      {dataLoading ? (
        <div className="mt-3 text-xs text-emerald-100/70">Loading</div>
      ) : dataError ? (
        <div className="mt-3 text-xs text-red-200">
          Unable to load sales cycle duration.
        </div>
      ) : salesCycle.byYear.size === 0 ? (
        <div className="mt-3 text-xs text-emerald-100/70">
          No sales cycle data for this filter selection.
        </div>
      ) : null}
    </div>
  );
}

/**
 * CLV vs CAC Comparison Card
 */
export function CLVvsCAC({ clvCacRatio, avgClv, avgCac, dataLoading, dataError }) {
  return (
    <div className="bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
      <div className="flex items-center justify-between flex-wrap gap-3 mb-4">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-sm font-semibold text-white">CLV vs CAC</h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Compares Customer Lifetime Value (CLV) with Customer Acquisition Cost (CAC). CLV is the total value a customer brings over their lifetime, while CAC is the cost to acquire them. The ratio shows return on acquisition investment.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/70">
            Averages based on current filters; ratio shows return per acquisition dollar.
          </p>
        </div>
        {/* CLV:CAC ratio */}
        <div className="text-2xl font-bold text-white">
          {dataLoading || dataError || clvCacRatio === null || Number.isNaN(clvCacRatio)
            ? ""
            : `${clvCacRatio.toFixed(2)}`}
        </div>
      </div>
      {/* 3 small cards for Avg CLV, Avg CAC, and ratio */}
      <div className="grid gap-3 sm:grid-cols-3">
        {/* Avg CLV */}
        <div className="rounded-xl border border-emerald-500/30 bg-black/10 px-4 py-3">
          <div className="text-xs uppercase tracking-wide text-emerald-200/80">
            Avg CLV
          </div>
          <div className="mt-2 text-xl font-semibold text-white">
            {dataLoading || dataError || avgClv === null || Number.isNaN(avgClv)
              ? ""
              : `$${Math.round(avgClv).toLocaleString()}`}
          </div>
        </div>
        {/* Avg CAC */}
        <div className="rounded-xl border border-emerald-500/30 bg-black/10 px-4 py-3">
          <div className="text-xs uppercase tracking-wide text-emerald-200/80">
            Avg CAC
          </div>
          <div className="mt-2 text-xl font-semibold text-white">
            {dataLoading || dataError || avgCac === null || Number.isNaN(avgCac)
              ? ""
              : `$${Math.round(avgCac).toLocaleString()}`}
          </div>
        </div>
        {/* CLV:CAC ratio */}
        <div className="rounded-xl border border-emerald-500/30 bg-black/10 px-4 py-3">
          <div className="text-xs uppercase tracking-wide text-emerald-200/80">
            CLV : CAC
          </div>
          <div className="mt-2 text-xl font-semibold text-white">
            {dataLoading || dataError || clvCacRatio === null || Number.isNaN(clvCacRatio)
              ? ""
              : `${clvCacRatio.toFixed(2)}`}
          </div>
        </div>
      </div>
    </div>
  );
}

