/**
 * ============================================================================
 * File: IntegratedDashboard.jsx
 * Author: Sivasankaran Rajasekaran
 * Last Edited Date: 12-05-2025
 * ============================================================================
 * 
 * OVERVIEW:
 * This component implements the Integrated CRM & ERP Analytics Dashboard,
 * providing a unified view of key performance indicators across both customer
 * relationship management (CRM) and enterprise resource planning (ERP) systems.
 * The dashboard combines funnel velocity metrics from CRM with supply chain
 * insights from ERP, offering end-to-end visibility into business operations.
 * 
 * KEY FEATURES:
 * - Unified KPIs across CRM and ERP systems
 * - Supply chain geographic visualization (bubble map by location)
 * - Process trend analysis by Purchase Order (PO)
 * - Risk summary and assessment
 * - On-time delivery tracking
 * - Lead time and cycle time analysis
 * - Interactive filters: Customer, Part, Supplier, Year
 * - Location footprint visualization with role-based color coding
 * 
 * COMPONENT STRUCTURE:
 * - Main orchestrator component
 * - Uses modular components for filters, KPIs, charts, and risk summary
 * - Centralized data processing logic
 * - Clean separation of concerns
 * ============================================================================
 */

import React, { useEffect, useMemo, useState } from "react";
import logger from "../lib/logger";
import { fetchIntegratedData } from "../lib/dataService";
import {
  processIntegratedData,
  prepareChartData,
  summarizeRisk,
} from "./integrated/integratedDataProcessing";
import {
  IntegratedFilters,
  KPICards,
  RiskSummaryCard,
} from "./integrated/integratedFilters";
import {
  ProcessTrendChart,
  BubbleSupplyMap,
} from "./integrated/integratedCharts";

function IntegratedDashboard({ onBack }) {
  // Log dashboard navigation
  useEffect(() => {
    logger.route("GET", "/integrated", 200, { dashboard: "IntegratedDashboard" });
    logger.info("IntegratedDashboard mounted", { dashboard: "IntegratedDashboard" });
  }, []);

  const [rows, setRows] = useState([]);
  const [dataLoading, setDataLoading] = useState(true);
  const [dataError, setDataError] = useState(null);
  // Missing columns (non-critical, data still loads)
  const [missingColumns, setMissingColumns] = useState([]);
  // Force a customer selection to avoid loading every PO; will auto-populate after fetch
  const [filters, setFilters] = useState({
    customer: "",
    part: "All",
    supplier: "All",
    year: "All",
  });

  // Load Integrated data using centralized data service
  useEffect(() => {
    const loadData = async () => {
      setDataLoading(true);
      setDataError(null);
      setMissingColumns([]);

      try {
        const { data, missingColumns: missing } = await fetchIntegratedData();
        setRows(data);
        setMissingColumns(missing || []);
      } catch (err) {
        setDataError(err.message ?? "Failed to load data");
      } finally {
        setDataLoading(false);
      }
    };
    loadData();
  }, []);

  // Process data using centralized processing logic
  const processedData = useMemo(
    () =>
      processIntegratedData({
        rows,
        filters,
        dataLoading,
        dataError,
        missingColumns,
      }),
    [rows, filters, dataLoading, dataError, missingColumns]
  );

  // Prepare chart data with optional monthly aggregation
  const chartData = useMemo(
    () => prepareChartData(processedData.poLevelData, filters),
    [processedData.poLevelData, filters]
  );

  // Auto-select first customer option once available
  useEffect(() => {
    if (!filters.customer && processedData.customerOptions.length > 0) {
      setFilters((prev) => ({
        ...prev,
        customer: processedData.customerOptions[0],
        part: "All",
        supplier: "All",
        year: "All",
      }));
    }
    // If current customer is no longer available, fall back to first option
    if (
      filters.customer &&
      processedData.customerOptions.length > 0 &&
      !processedData.customerOptions.includes(filters.customer)
    ) {
      setFilters((prev) => ({
        ...prev,
        customer: processedData.customerOptions[0],
        part: "All",
        supplier: "All",
        year: "All",
      }));
    }
  }, [processedData.customerOptions, filters.customer]);

  // Handler functions for filter changes
  const handleCustomerChange = (newValue) => {
    setFilters((prev) => ({
      ...prev,
      customer: newValue,
      part: "All",
      supplier: "All",
    }));
  };

  const handlePartChange = (newValue) => {
    setFilters((prev) => ({
      ...prev,
      part: newValue,
      supplier: "All",
    }));
  };

  const handleSupplierChange = (newValue) => {
    setFilters((prev) => ({
      ...prev,
      supplier: newValue,
    }));
  };

  const handleYearChange = (newValue) => {
    setFilters((prev) => ({
      ...prev,
      year: newValue,
    }));
  };

  const handleResetFilters = () => {
    setFilters({
      customer: processedData.customerOptions.length > 0 ? processedData.customerOptions[0] : "",
      part: "All",
      supplier: "All",
      year: "All",
    });
  };

  // Risk summary calculations
  const riskColor =
    processedData.riskSummary.riskLevel === "High Risk"
      ? "text-red-300"
      : processedData.riskSummary.riskLevel === "Medium Risk"
      ? "text-yellow-300"
      : processedData.riskSummary.riskLevel === "Safe"
      ? "text-emerald-300"
      : "text-emerald-100";
  const canShowRisk =
    Boolean(filters.customer) &&
    filters.part !== "All" &&
    filters.supplier !== "All";
  const distribution = useMemo(
    () => (canShowRisk ? summarizeRisk(processedData.poLevelData) : null),
    [canShowRisk, processedData.poLevelData]
  );

  // Reset dependent filters if invalid
  useEffect(() => {
    if (filters.part !== "All" && !processedData.partOptions.includes(filters.part)) {
      setFilters((prev) => ({ ...prev, part: "All", supplier: "All" }));
    }
  }, [processedData.partOptions, filters.part]);
  useEffect(() => {
    if (filters.supplier !== "All" && !processedData.supplierOptions.includes(filters.supplier)) {
      setFilters((prev) => ({ ...prev, supplier: "All" }));
    }
  }, [processedData.supplierOptions, filters.supplier]);

  return (
    <section className="max-w-7xl mx-auto mt-10 pt-2 pb-16 min-h-screen">
      <div className="rounded-3xl border border-emerald-500/40 bg-gradient-to-b from-emerald-900 via-emerald-800 to-emerald-900 px-5 py-6 shadow-emerald-900/40 shadow-2xl md:px-8 md:py-8">
        <div className="flex items-center justify-between gap-4">
          <button
            onClick={() => {
              logger.route("GET", "/", 200, { from: "IntegratedDashboard", action: "back" });
              logger.info("Navigating back to main dashboard", { from: "IntegratedDashboard" });
              onBack();
            }}
            className="inline-flex items-center gap-2 text-sm text-emerald-100 hover:text-emerald-50
                       border border-emerald-400/40 rounded-full px-3 py-1 bg-emerald-900/40
                       hover:bg-emerald-800/60 transition"
          >
            <span className="text-lg leading-none"></span>
            <span>Back to dashboard</span>
          </button>
          <div className="text-xs text-emerald-100/70">
            Home / Integrated Analytics
          </div>
        </div>
        <div className="mt-6">
          <p className="inline-flex items-center gap-2 rounded-full bg-emerald-900/60 border border-emerald-400/40 px-4 py-1 text-xs text-emerald-100">
            <span className="inline-block w-2 h-2 rounded-full bg-emerald-400" />
            CRM &amp; ERP Integration
          </p>
          <h2 className="mt-3 text-2xl md:text-3xl font-bold text-white tracking-tight">
            Cross-System Cost, Cycle &amp; Quality KPIs
          </h2>
          <p className="mt-2 text-sm text-emerald-100/80 max-w-2xl">
            Track PO-level cost, lead time, defects, OEE, risk, and geo flow with
            synchronized customer, part, and supplier filters.
          </p>
        </div>
        {/* Error message if data failed to load (schema/table missing) */}
        {dataError && (
          <p className="mt-4 text-xs text-red-200">
            Failed to load data: {dataError}
          </p>
        )}

        {/* Warning message if columns are missing (non-critical) */}
        {!dataError && missingColumns.length > 0 && (
          <p className="mt-4 text-xs text-yellow-200">
            Warning: Column(s) '{missingColumns.join("', '")}' do not exist in table 'crm_erp_funnel_view' (schema 'crm'). 
            Some metrics may show "". Please check your database schema.
          </p>
        )}

        {/* Filters and KPI Cards */}
        <IntegratedFilters
          filters={filters}
          customerOptions={processedData.customerOptions}
          partOptions={processedData.partOptions}
          supplierOptions={processedData.supplierOptions}
          yearOptions={processedData.yearOptions}
          filteredCount={processedData.filteredCount}
          totalLoadedCount={processedData.totalLoadedCount}
          onCustomerChange={handleCustomerChange}
          onPartChange={handlePartChange}
          onSupplierChange={handleSupplierChange}
          onYearChange={handleYearChange}
          onResetFilters={handleResetFilters}
        />

        <KPICards
          poLevelData={processedData.poLevelData}
          onTimeDeliveryPct={processedData.onTimeDeliveryPct}
          avgLeadTime={processedData.avgLeadTime}
        />

        {/* Risk Summary */}
        <RiskSummaryCard
          distribution={distribution}
          canShowRisk={canShowRisk}
          riskColor={riskColor}
          />

        {/* Charts and Visualizations */}
        <ProcessTrendChart
          chartData={chartData}
          dataLoading={dataLoading}
          dataError={dataError}
         />

        <BubbleSupplyMap
          nodes={processedData.bubbleNodes}
          filters={filters}
        />
      </div>
    </section>
  );
}

export default IntegratedDashboard;