import React from "react";
import logger from "../../lib/logger";

/**
 * ERP Filters Component
 * Handles filter UI (Year, Region, Location) and reset functionality
 */
export function ERPFilters({
  selectedYear,
  selectedRegion,
  selectedLocation,
  yearOptions,
  regionOptions,
  locationOptions,
  filteredCount,
  totalLoadedCount,
  onYearChange,
  onRegionChange,
  onLocationChange,
  onResetFilters,
}) {
  return (
    <>
      {/* Record Count Display */}
      <div className="mt-6 flex items-center justify-between flex-wrap gap-4">
        <div className="bg-white/10 backdrop-blur-sm border border-white/20 rounded-lg px-4 py-2 text-sm">
          <span className="text-white font-semibold">
            {filteredCount.toLocaleString()}
          </span>
          <span className="text-emerald-100/70 mx-1">of</span>
          <span className="text-white font-semibold">
            {totalLoadedCount.toLocaleString()}
          </span>
          <span className="text-emerald-100/70 ml-1">records loaded</span>
        </div>
        {/* Reset Filters Button */}
        <button
          onClick={() => {
            const oldFilters = { year: selectedYear, region: selectedRegion, location: selectedLocation };
            onResetFilters();
            logger.info("Filters reset", {
              dashboard: "ERPDashboard",
              oldFilters,
              newFilters: { year: "All", region: "All", location: "All" }
            });
          }}
          className="bg-emerald-700 hover:bg-emerald-600 text-white rounded-md px-4 py-2 text-sm font-medium transition-all duration-200 shadow-md hover:shadow-lg flex items-center gap-2"
        >
          <span></span>
          <span>Reset Filters</span>
        </button>
      </div>

      {/* FILTER BAR */}
      <div className="mt-6">
        <div className="grid gap-4 md:grid-cols-3">
          <div className="flex flex-col">
            <label className="text-xs text-emerald-100/80 mb-1">Year</label>
            <select
              value={selectedYear}
              onChange={(e) => {
                const newValue = e.target.value;
                const oldValue = selectedYear;
                onYearChange(newValue);
                logger.info("Filter changed: Year", {
                  dashboard: "ERPDashboard",
                  filter: "year",
                  oldValue,
                  newValue
                });
              }}
              className="bg-[#050908]/70 border border-emerald-500/40 rounded-xl px-3 py-2 text-sm text-emerald-50
                         focus:outline-none focus:ring-2 focus:ring-emerald-400/60"
            >
              {yearOptions.map((year) => (
                <option key={year} value={year}>
                  {year}
                </option>
              ))}
            </select>
          </div>

          <div className="flex flex-col">
            <label className="text-xs text-emerald-100/80 mb-1">Region</label>
            <select
              value={selectedRegion}
              onChange={(e) => {
                const newValue = e.target.value;
                const oldValue = selectedRegion;
                onRegionChange(newValue);
                logger.info("Filter changed: Region", {
                  dashboard: "ERPDashboard",
                  filter: "region",
                  oldValue,
                  newValue,
                  note: "Location reset to All"
                });
              }}
              className="bg-[#050908]/70 border border-emerald-500/40 rounded-xl px-3 py-2 text-sm text-emerald-50
                         focus:outline-none focus:ring-2 focus:ring-emerald-400/60"
            >
              {regionOptions.map((region) => (
                <option key={region} value={region}>
                  {region}
                </option>
              ))}
            </select>
          </div>

          <div className="flex flex-col">
            <label className="text-xs text-emerald-100/80 mb-1">Location</label>
            <select
              value={selectedLocation}
              onChange={(e) => {
                const newValue = e.target.value;
                const oldValue = selectedLocation;
                onLocationChange(newValue);
                logger.info("Filter changed: Location", {
                  dashboard: "ERPDashboard",
                  filter: "location",
                  oldValue,
                  newValue
                });
              }}
              className="bg-[#050908]/70 border border-emerald-500/40 rounded-xl px-3 py-2 text-sm text-emerald-50
                         focus:outline-none focus:ring-2 focus:ring-emerald-400/60"
            >
              {locationOptions.map((loc) => (
                <option key={loc} value={loc}>
                  {loc}
                </option>
              ))}
            </select>
          </div>
        </div>
      </div>
    </>
  );
}

/**
 * KPI Cards Component
 * Displays the 3 KPI cards
 */
export function KPICards({ kpiCards, dataLoading, dataError }) {
  return (
    <div className="mt-8 grid gap-6 grid-cols-1 md:grid-cols-3">
      {kpiCards.map((kpi, index) => (
        <div
          key={kpi.id}
          className="bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-5 shadow-lg backdrop-blur-sm
                     flex flex-col justify-between
                     hover:border-emerald-300 hover:shadow-emerald-400/30 transition-all duration-300
                     hover:-translate-y-1"
          style={{
            animation: "fadeInUp 0.9s ease-out forwards",
            animationDelay: `${0.15 * (index + 1)}s`,
          }}
        >
          <div className="flex items-start justify-between">
            <div className="flex-1">
              <div className="flex items-center gap-2">
                <p className="text-xs uppercase tracking-wide text-emerald-200/80">
                  {kpi.label}
                </p>
                <div className="group relative">
                  <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
                  <div className="absolute bottom-full left-1/2 transform -translate-x-1/2 mb-2 w-48 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                    {kpi.id === "cost-good" && "Average cost to produce one good (non-defective) unit. Lower values indicate more cost-efficient production."}
                    {kpi.id === "cycle" && "Average time from purchase order creation to final movement in the supply chain. Shorter cycles indicate faster end-to-end processes."}
                    {kpi.id === "util" && "Percentage of received materials that are successfully converted into finished pieces. Higher percentages show better material utilization and production efficiency."}
                    <div className="absolute top-full left-1/2 transform -translate-x-1/2 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
                  </div>
                </div>
              </div>
              <p className="mt-3 text-xl md:text-2xl font-semibold text-white leading-tight break-words">
                {kpi.value}
              </p>
              <p className="mt-3 text-xs text-emerald-100/70">{kpi.helper}</p>
            </div>
            <div className="text-4xl text-emerald-400/60 ml-4">
              <i className={`fas ${kpi.icon || "fa-chart-line"}`}></i>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}