/**
 * Simple logger utility that provides structured logging methods
 * All logs are output to the browser console
 */
const logger = {
  /**
   * Log informational messages
   * @param {string} message - Log message
   * @param {Object} context - Additional context data
   */
  info(message, context = {}) {
    console.log(`[INFO] ${message}`, context);
  },

  /**
   * Log warning messages
   * @param {string} message - Log message
   * @param {Object} context - Additional context data
   */
  warn(message, context = {}) {
    console.warn(`[WARN] ${message}`, context);
  },

  /**
   * Log error messages
   * @param {string} message - Log message
   * @param {Error|Object} error - Error object or context data
   * @param {Object} context - Additional context data
   */
  error(message, error = null, context = {}) {
    if (error instanceof Error) {
      console.error(`[ERROR] ${message}`, error, context);
    } else {
      console.error(`[ERROR] ${message}`, error || context);
    }
  },

  /**
   * Log route/navigation events
   * @param {string} method - HTTP method (GET, POST, etc.)
   * @param {string} path - Route path
   * @param {number} status - HTTP status code
   * @param {Object} context - Additional context data
   */
  route(method, path, status, context = {}) {
    console.log(`[ROUTE] ${method} ${path} ${status}`, context);
  },

  /**
   * Log HTTP request events
   * @param {string} method - HTTP method
   * @param {string} url - Request URL
   * @param {Object} context - Additional context data
   */
  httpRequest(method, url, context = {}) {
    console.log(`[HTTP REQUEST] ${method} ${url}`, context);
  },

  /**
   * Log HTTP response events
   * @param {string} method - HTTP method
   * @param {string} url - Request URL
   * @param {string|number} status - HTTP status code or status text
   * @param {string} message - Response message
   * @param {number} duration - Request duration in milliseconds
   * @param {Object} context - Additional context data
   */
  httpResponse(method, url, status, message, duration, context = {}) {
    console.log(`[HTTP RESPONSE] ${method} ${url} ${status} ${message} (${duration}ms)`, context);
  },

  /**
   * Log performance metrics
   * @param {string} message - Performance message
   * @param {number} duration - Duration in milliseconds
   * @param {Object} context - Additional context data
   */
  performance(message, duration, context = {}) {
    console.log(`[PERFORMANCE] ${message} (${duration}ms)`, context);
  },
};

export default logger;
