import supabase from "./supabaseClient";
import logger from "./logger";

/**
 * Categorize and enhance Supabase service errors with user-friendly messages
 * Handles: Service Unavailable, Authentication Failures, Quota Exceeded
 * @param {Object} error - Supabase error object
 * @param {string} dashboard - Dashboard name for logging context
 * @returns {Error} Enhanced error with user-friendly message, or original error if not categorized
 */
function categorizeSupabaseServiceError(error, dashboard) {
  // Extract error details
  const errorMessage = error?.message || "";
  const errorCode = error?.code || "";
  const statusCode = error?.status || error?.statusCode || null;

  // Check for Service Unavailable (503, 502, 500)
  if (
    statusCode === 503 ||
    statusCode === 502 ||
    statusCode === 500 ||
    errorMessage.toLowerCase().includes("service unavailable") ||
    errorMessage.toLowerCase().includes("bad gateway") ||
    errorMessage.toLowerCase().includes("internal server error")
  ) {
    logger.warn("Service unavailable error detected", {
      dashboard,
      statusCode,
      errorCode,
      originalMessage: errorMessage,
    });

    const enhancedError = new Error(
      "Service temporarily unavailable. Please try again later."
    );
    enhancedError.originalError = error;
    enhancedError.errorType = "SERVICE_UNAVAILABLE";
    enhancedError.statusCode = statusCode;
    return enhancedError;
  }

  // Check for Authentication Failures (401, 403)
  if (
    statusCode === 401 ||
    statusCode === 403 ||
    errorMessage.toLowerCase().includes("unauthorized") ||
    errorMessage.toLowerCase().includes("forbidden") ||
    errorMessage.toLowerCase().includes("authentication") ||
    errorMessage.toLowerCase().includes("permission denied")
  ) {
    logger.warn("Authentication failure detected", {
      dashboard,
      statusCode,
      errorCode,
      originalMessage: errorMessage,
    });

    const enhancedError = new Error(
      "Authentication failed. Please refresh the page or contact support if the issue persists."
    );
    enhancedError.originalError = error;
    enhancedError.errorType = "AUTHENTICATION_FAILURE";
    enhancedError.statusCode = statusCode;
    return enhancedError;
  }

  // Check for Quota Exceeded
  if (
    errorMessage.toLowerCase().includes("quota") ||
    errorMessage.toLowerCase().includes("quota exceeded") ||
    errorMessage.toLowerCase().includes("limit exceeded") ||
    errorMessage.toLowerCase().includes("usage limit") ||
    errorMessage.toLowerCase().includes("billing") ||
    (statusCode === 429 && errorMessage.toLowerCase().includes("quota"))
  ) {
    logger.warn("Quota exceeded error detected", {
      dashboard,
      statusCode,
      errorCode,
      originalMessage: errorMessage,
    });

    const enhancedError = new Error(
      "Service quota exceeded. Please contact support or upgrade your plan."
    );
    enhancedError.originalError = error;
    enhancedError.errorType = "QUOTA_EXCEEDED";
    enhancedError.statusCode = statusCode;
    return enhancedError;
  }

  // If error doesn't match any category, return original error
  return error;
}

/**
 * Validate schema, table, and columns before fetching
 * Throws error if schema/table is missing, returns missing columns if only columns are missing
 * @param {string} dashboard - Dashboard name for logging
 * @param {string} schema - Supabase schema name
 * @param {string} table - Supabase table/view name
 * @param {string[]} columns - Array of column names to validate
 * @returns {Promise<{missingColumns: string[]}>} Object with array of missing column names
 * @throws {Error} Clear error message if schema or table is missing
 */
async function validateSchemaAndColumns(dashboard, schema, table, columns) {
  logger.info("Validating schema and columns", {
    dashboard,
    schema,
    table,
    columnCount: columns.length,
  });

  // Test 1: Check if schema and table exist by doing a minimal query
  // THIS MUST THROW IF MISSING (critical error)
  try {
    const { error: schemaTableError } = await supabase
      .schema(schema)
      .from(table)
      .select("*")
      .limit(0);

    if (schemaTableError) {
      const errorMessage = schemaTableError.message || "";
      
      // Check for schema-related errors
      if (
        errorMessage.includes("schema") ||
        errorMessage.includes("does not exist") ||
        errorMessage.includes("permission denied for schema")
      ) {
        throw new Error(
          `Schema '${schema}' does not exist or you don't have permission to access it. Please check your database configuration.`
        );
      }

      // Check for table/view-related errors
      if (
        errorMessage.includes("relation") ||
        errorMessage.includes("table") ||
        errorMessage.includes("view") ||
        errorMessage.includes("does not exist")
      ) {
        throw new Error(
          `Table or view '${table}' does not exist in schema '${schema}'. Please check your database configuration.`
        );
      }

      // If we can't determine the exact issue, throw the original error
      throw new Error(
        `Cannot access table '${table}' in schema '${schema}': ${errorMessage}`
      );
    }
  } catch (err) {
    // If it's already our custom error, re-throw it
    if (err.message.includes("Schema") || err.message.includes("Table")) {
      throw err;
    }
    // Otherwise, wrap it
    throw new Error(
      `Validation failed for schema '${schema}' and table '${table}': ${err.message}`
    );
  }

  // Test 2: Check if all required columns exist
  // THIS RETURNS MISSING COLUMNS (non-critical, continue with available columns)
  const missingColumns = [];
  
  try {
    const { error: columnsError } = await supabase
      .schema(schema)
      .from(table)
      .select(columns.join(", "))
      .limit(0);

    if (columnsError) {
      const errorMessage = columnsError.message || "";
      
      // Check for column-related errors
      if (
        errorMessage.includes("column") ||
        errorMessage.includes("does not exist")
      ) {
        // Identify which columns are missing by testing each one
        for (const column of columns) {
          try {
            const { error: singleColumnError } = await supabase
              .schema(schema)
              .from(table)
              .select(column)
              .limit(0);

            if (singleColumnError) {
              missingColumns.push(column);
            }
          } catch (err) {
            missingColumns.push(column);
          }
        }
      }
    }
  } catch (err) {
    // If validation fails for other reasons, log but don't throw
    logger.warn("Column validation encountered an issue", {
      dashboard,
      schema,
      table,
      error: err.message,
    });
  }

  if (missingColumns.length > 0) {
    logger.warn("Some columns are missing", {
      dashboard,
      schema,
      table,
      missingColumns,
      availableColumns: columns.filter(c => !missingColumns.includes(c)),
    });
  } else {
    logger.info("Schema and columns validation passed", {
      dashboard,
      schema,
      table,
      columnsValidated: columns.length,
    });
  }

  return { missingColumns };
}

/**
 * Generic chunked data fetcher
 * @param {Object} config - Configuration object
 * @param {string} config.dashboard - Dashboard name for logging
 * @param {string} config.schema - Supabase schema name
 * @param {string} config.table - Supabase table/view name
 * @param {string[]} config.columns - Array of column names to select
 * @param {number} config.chunkSize - Number of rows per chunk (default: 20000)
 * @param {number} config.maxRows - Maximum total rows to fetch (default: 140000)
 * @returns {Promise<{data: Array, missingColumns: string[]}>} Object with fetched rows and missing columns
 */
async function fetchChunkedData({
  dashboard,
  schema,
  table,
  columns,
  chunkSize = 20000,
  maxRows = 140000,
}) {
  const startTime = performance.now();
  
  logger.info("Data fetch started", {
    dashboard,
    schema,
    table,
  });

  try {
    // Validate schema, table, and columns before fetching
    // This will throw if schema/table is missing, or return missing columns
    const { missingColumns } = await validateSchemaAndColumns(dashboard, schema, table, columns);
    
    // Filter out missing columns - only fetch columns that exist
    const availableColumns = columns.filter(col => !missingColumns.includes(col));
    
    if (missingColumns.length > 0) {
      logger.warn("Fetching data with missing columns", {
        dashboard,
        missingColumns,
        availableColumns,
      });
    }

    let offset = 0;
    let aggregated = [];
    let chunkCount = 0;

    while (true) {
      chunkCount++;
      const chunkStartTime = performance.now();

      // Build query URL for logging (using available columns only)
      const queryUrl = `${process.env.REACT_APP_SUPABASE_URL}/rest/v1/${table}?select=${availableColumns.join(",")}&offset=${offset}&limit=${chunkSize}`;
      
      logger.httpRequest("GET", queryUrl, {
        dashboard,
        chunk: chunkCount,
        offset,
        limit: chunkSize,
      });

      // Fetch chunk of data from Supabase (using available columns only)
      const { data, error } = await supabase
        .schema(schema)
        .from(table)
        .select(availableColumns.join(", "))
        .range(offset, offset + chunkSize - 1);

      const chunkDuration = Math.round(performance.now() - chunkStartTime);

      // Handle errors
      if (error) {
        // Categorize Supabase service errors (Service Unavailable, Auth Failure, Quota Exceeded)
        const categorizedError = categorizeSupabaseServiceError(error, dashboard);
        
        logger.httpResponse("GET", queryUrl, "ERROR", categorizedError.message, chunkDuration, {
          dashboard,
          chunk: chunkCount,
          error: categorizedError.message,
          errorType: categorizedError.errorType || "UNKNOWN",
          originalError: error.message,
          statusCode: categorizedError.statusCode || error?.status || null,
        });
        
        throw categorizedError;
      }

      // Log successful response
      logger.httpResponse("GET", queryUrl, "200", "OK", chunkDuration, {
        dashboard,
        chunk: chunkCount,
        rowsReturned: data?.length || 0,
      });

      // Ensure we always have an array even if data is null
      const batch = data || [];
      aggregated = aggregated.concat(batch);

      // Stop if we got less than chunkSize (last page) or reached maxRows
      if (batch.length < chunkSize || aggregated.length >= maxRows) {
        break;
      }

      // Move offset forward for next chunk
      offset += chunkSize;
    }

    const totalDuration = Math.round(performance.now() - startTime);

    logger.performance("Data fetch completed", totalDuration, {
      dashboard,
      totalRows: aggregated.length,
      chunks: chunkCount,
    });

    logger.info("Data fetch successful", {
      dashboard,
      totalRows: aggregated.length,
      chunks: chunkCount,
      duration: `${totalDuration}ms`,
      missingColumns: missingColumns.length > 0 ? missingColumns : undefined,
    });

    return {
      data: aggregated,
      missingColumns,
    };
  } catch (err) {
    const totalDuration = Math.round(performance.now() - startTime);
    
    // If error is already categorized, use it; otherwise try to categorize it
    const finalError = err.errorType ? err : categorizeSupabaseServiceError(err, dashboard);
    
    logger.error("Data fetch failed", finalError, {
      dashboard,
      duration: `${totalDuration}ms`,
      schema,
      table,
      errorType: finalError.errorType || "UNKNOWN",
      statusCode: finalError.statusCode || err?.status || null,
    });

    throw finalError;
  }
}

/**
 * Fetch CRM Marketing Analytics data
 * Used by: CRMDashboard
 * @returns {Promise<{data: Array, missingColumns: string[]}>} Object with CRM funnel data rows and missing columns
 */
export async function fetchCRMData() {
  return fetchChunkedData({
    dashboard: "CRMDashboard",
    schema: "crm",
    table: "crm_funnel_view",
    columns: [
      "campaign_id",
      "campaign_name",
      "channel",
      "year",
      "marketing_region",
      "marketing_location",
      "budget",
      "spend",
      "leads_generated",
      "leads_converted",
      "start_date",
      "response_time_hours",
      "lead_id",
      "opportunity_id",
      "customer_id",
      "created_date",
      "total_booked_revenue",
      "cac",
      "clv",
    ],
    chunkSize: 20000,
    maxRows: 140000,
  });
}

