import React from "react";
import logger from "../../lib/logger";

/**
 * Integrated Filters Component
 * Handles filter UI (Customer, Part, Vendor, Year) and reset functionality
 */
export function IntegratedFilters({
  filters,
  customerOptions,
  partOptions,
  supplierOptions,
  yearOptions,
  filteredCount,
  totalLoadedCount,
  onCustomerChange,
  onPartChange,
  onSupplierChange,
  onYearChange,
  onResetFilters,
}) {
  return (
    <>
      {/* Record Count Display */}
      <div className="mt-6 flex items-center justify-between flex-wrap gap-4">
        <div className="bg-white/10 backdrop-blur-sm border border-white/20 rounded-lg px-4 py-2 text-sm">
          <span className="text-white font-semibold">
            {filteredCount.toLocaleString()}
          </span>
          <span className="text-emerald-100/70 mx-1">of</span>
          <span className="text-white font-semibold">
            {totalLoadedCount.toLocaleString()}
          </span>
          <span className="text-emerald-100/70 ml-1">records loaded</span>
        </div>
        {/* Reset Filters Button */}
        <button
          onClick={() => {
            const oldFilters = { ...filters };
            onResetFilters();
            logger.info("Filters reset", {
              dashboard: "IntegratedDashboard",
              oldFilters,
              newFilters: {
                customer: customerOptions.length > 0 ? customerOptions[0] : "",
                part: "All",
                supplier: "All",
                year: "All",
              }
            });
          }}
          className="bg-emerald-700 hover:bg-emerald-600 text-white rounded-md px-4 py-2 text-sm font-medium transition-all duration-200 shadow-md hover:shadow-lg flex items-center gap-2"
        >
          <span></span>
          <span>Reset Filters</span>
        </button>
      </div>

      {/* Filters */}
      <div className="mt-6">
        <div className="grid gap-4 md:grid-cols-4">
          <div className="flex flex-col">
            <label className="text-xs text-emerald-100/80 mb-1">Customer</label>
            <select
              value={filters.customer}
              onChange={(e) => {
                const newValue = e.target.value;
                const oldValue = filters.customer;
                onCustomerChange(newValue);
                logger.info("Filter changed: Customer", {
                  dashboard: "IntegratedDashboard",
                  filter: "customer",
                  oldValue,
                  newValue,
                  note: "Part and Supplier reset to All"
                });
              }}
              className="bg-[#050908]/70 border border-emerald-500/40 rounded-xl px-3 py-2 text-sm text-emerald-50 focus:outline-none focus:ring-2 focus:ring-emerald-400/60"
            >
              {customerOptions.map((c) => (
                <option key={c} value={c}>
                  {c}
                </option>
              ))}
            </select>
          </div>
          <div className="flex flex-col">
            <label className="text-xs text-emerald-100/80 mb-1">Part</label>
            <select
              value={filters.part}
              onChange={(e) => {
                const newValue = e.target.value;
                const oldValue = filters.part;
                onPartChange(newValue);
                logger.info("Filter changed: Part", {
                  dashboard: "IntegratedDashboard",
                  filter: "part",
                  oldValue,
                  newValue,
                  note: "Supplier reset to All"
                });
              }}
              className="bg-[#050908]/70 border border-emerald-500/40 rounded-xl px-3 py-2 text-sm text-emerald-50 focus:outline-none focus:ring-2 focus:ring-emerald-400/60"
            >
              <option value="All">All</option>
              {partOptions.map((p) => (
                <option key={p} value={p}>
                  {p}
                </option>
              ))}
            </select>
          </div>
          <div className="flex flex-col">
            <label className="text-xs text-emerald-100/80 mb-1">Vendor</label>
            <select
              value={filters.supplier}
              onChange={(e) => {
                const newValue = e.target.value;
                const oldValue = filters.supplier;
                onSupplierChange(newValue);
                logger.info("Filter changed: Supplier", {
                  dashboard: "IntegratedDashboard",
                  filter: "supplier",
                  oldValue,
                  newValue
                });
              }}
              className="bg-[#050908]/70 border border-emerald-500/40 rounded-xl px-3 py-2 text-sm text-emerald-50 focus:outline-none focus:ring-2 focus:ring-emerald-400/60"
            >
              <option value="All">All</option>
              {supplierOptions.map((s) => (
                <option key={s} value={s}>
                  {s}
                </option>
              ))}
            </select>
          </div>
          <div className="flex flex-col">
            <label className="text-xs text-emerald-100/80 mb-1">Year</label>
            <select
              value={filters.year}
              onChange={(e) => {
                const newValue = e.target.value;
                const oldValue = filters.year;
                onYearChange(newValue);
                logger.info("Filter changed: Year", {
                  dashboard: "IntegratedDashboard",
                  filter: "year",
                  oldValue,
                  newValue
                });
              }}
              className="bg-[#050908]/70 border border-emerald-500/40 rounded-xl px-3 py-2 text-sm text-emerald-50 focus:outline-none focus:ring-2 focus:ring-emerald-400/60"
            >
              <option value="All">All</option>
              {yearOptions.map((y) => (
                <option key={y} value={y}>
                  {y}
                </option>
              ))}
            </select>
          </div>
        </div>
      </div>
    </>
  );
}

/**
 * KPI Cards Component
 * Displays the 3 KPI cards: Total POs, On-Time Delivery, Average Lead Time
 */
export function KPICards({ poLevelData, onTimeDeliveryPct, avgLeadTime }) {
  return (
    <div className="mt-8 grid gap-4 md:grid-cols-1 lg:grid-cols-3">
      {/* Total POs Card */}
      <div className="bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
        <div className="flex items-center justify-between">
          <div className="flex-1">
            <div className="flex items-center gap-2 mb-1">
              <p className="text-xs uppercase tracking-wide text-emerald-200/70">
                Total Purchase Orders
              </p>
              <div className="group relative">
                <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
                <div className="absolute bottom-full left-1/2 transform -translate-x-1/2 mb-2 w-48 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                  Shows the total number of unique purchase orders for your selected filters. Each PO represents a distinct order in your supply chain.
                  <div className="absolute top-full left-1/2 transform -translate-x-1/2 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
                </div>
              </div>
            </div>
            <p className="text-3xl font-bold text-white">
              {poLevelData.length.toLocaleString()}
            </p>
            <p className="text-xs text-emerald-100/70 mt-1">
              Unique POs for selected filters
            </p>
          </div>
          <div className="text-5xl text-emerald-400/60">
            <i className="fas fa-file-invoice"></i>
          </div>
        </div>
      </div>
      
      {/* On-Time Delivery % Card */}
      <div className="bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
        <div className="flex items-center justify-between">
          <div className="flex-1">
            <div className="flex items-center gap-2 mb-1">
              <p className="text-xs uppercase tracking-wide text-emerald-200/70">
                On-Time Delivery
              </p>
              <div className="group relative">
                <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
                <div className="absolute bottom-full left-1/2 transform -translate-x-1/2 mb-2 w-48 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                  Percentage of deliveries that arrived on or before the planned delivery date. Higher percentages indicate better customer commitment performance.
                  <div className="absolute top-full left-1/2 transform -translate-x-1/2 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
                </div>
              </div>
            </div>
            <p className="text-3xl font-bold text-white">
              {onTimeDeliveryPct !== null && !Number.isNaN(onTimeDeliveryPct)
                ? `${onTimeDeliveryPct.toFixed(1)}%`
                : ""}
            </p>
            <p className="text-xs text-emerald-100/70 mt-1">
              Deliveries on or before planned date
            </p>
          </div>
          <div className="text-5xl text-emerald-400/60">
            <i className="fas fa-calendar-check"></i>
          </div>
        </div>
      </div>
      
      {/* Average Lead Time Card */}
      <div className="bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
        <div className="flex items-center justify-between">
          <div className="flex-1">
            <div className="flex items-center gap-2 mb-1">
              <p className="text-xs uppercase tracking-wide text-emerald-200/70">
                Average Lead Time
              </p>
              <div className="group relative">
                <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
                <div className="absolute bottom-full left-1/2 transform -translate-x-1/2 mb-2 w-48 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                  Average number of days from purchase order creation to delivery. Lower lead times indicate more efficient supply chain operations.
                  <div className="absolute top-full left-1/2 transform -translate-x-1/2 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
                </div>
              </div>
            </div>
            <p className="text-3xl font-bold text-white">
              {avgLeadTime !== null && !Number.isNaN(avgLeadTime)
                ? `${avgLeadTime.toFixed(1)} days`
                : ""}
            </p>
            <p className="text-xs text-emerald-100/70 mt-1">
              Supply chain efficiency
            </p>
          </div>
          <div className="text-5xl text-emerald-400/60">
            <i className="fas fa-tachometer-alt"></i>
          </div>
        </div>
      </div>
    </div>
  );
}

/**
 * Risk Summary Card Component
 */
export function RiskSummaryCard({ distribution, canShowRisk, riskColor }) {
  return (
    <div className="mt-8 bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
      <div className="flex items-center justify-between flex-wrap gap-3">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-sm font-semibold text-white">Risk Summary</h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Shows the distribution of risk levels across your purchase orders. Risk is categorized as Safe, Medium, or High based on aggregated risk scores. Helps identify potential supply chain vulnerabilities.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/70">
            Based on risk_score and risk_level provided by the integrated data.
          </p>
        </div>
      </div>
      {!canShowRisk ? (
        <div className="text-xs text-emerald-100/70 mt-2">
          Select customer, part, and vendor to see risk.
        </div>
      ) : !distribution ? (
        <div className="text-xs text-emerald-100/70 mt-2">
          No data for this selection.
        </div>
      ) : (
        <div className="grid gap-3 sm:grid-cols-4 mt-4 text-sm text-emerald-50">
          <div>
            <div className="text-xs uppercase tracking-wide text-emerald-200/70">
              Overall
            </div>
            <div className={`text-xl font-bold ${riskColor}`}>
              {distribution.overall_level}
            </div>
            <div className="text-xs text-emerald-100/70">
              Avg score:{" "}
              {distribution.avg_risk_score !== null
                ? distribution.avg_risk_score.toFixed(1)
                : ""}
            </div>
          </div>
          <div>
            <div className="text-xs uppercase tracking-wide text-emerald-200/70">
              Safe
            </div>
            <div className="text-lg font-semibold text-emerald-200">
              {distribution.safe_count}
            </div>
          </div>
          <div>
            <div className="text-xs uppercase tracking-wide text-emerald-200/70">
              Medium
            </div>
            <div className="text-lg font-semibold text-yellow-300">
              {distribution.medium_count}
            </div>
          </div>
          <div>
            <div className="text-xs uppercase tracking-wide text-emerald-200/70">
              High
            </div>
            <div className="text-lg font-semibold text-red-300">
              {distribution.high_count} (
              {(distribution.high_pct * 100).toFixed(1)}%)
            </div>
          </div>
        </div>
      )}
    </div>
  );
}



