/**
 * ============================================================================
 * File: ERPDashboard.jsx
 * Author: Sivasankaran Rajasekaran
 * Last Edited Date: 12-05-2025
 * ============================================================================
 * 
 * OVERVIEW:
 * This component implements the ERP (Enterprise Resource Planning) Analytics
 * Dashboard, providing comprehensive insights into procurement, production,
 * and inventory operations. The dashboard follows a similar pattern to the
 * CRM dashboard with server-side filtering and client-side KPI computation.
 * 
 * KEY FEATURES:
 * - Real-time data visualization for ERP metrics
 * - Interactive filters for year, region, and location
 * - Key Performance Indicators (KPIs):
 *   - Cost per goods unit
 *   - Average cycle time
 *   - Resource utilization rates
 * - Multiple chart types: Scatter plots, Line charts, and Bar charts
 * - Modular architecture with separated concerns
 * 
 * COMPONENT STRUCTURE:
 * - Main orchestrator component
 * - Uses modular components for filters, KPIs, and charts
 * - Centralized data processing logic
 * - Clean separation of concerns
 * ============================================================================
 */

import React, { useEffect, useMemo, useState } from "react";
import logger from "../lib/logger";
import { fetchERPData } from "../lib/dataService";
import { processERPData } from "./erp/erpDataProcessing";
import { ERPFilters, KPICards } from "./erp/erpFilters";
import {
  VendorQualityYieldChart,
  OnTimeFulfillmentHeatmap,
  CostBreakdownChart,
  CycleTimeTrendChart,
} from "./erp/erpCharts";

// ERP dashboard mirrors the CRM pattern:
// - Apply filters server-side (year/region/location)
// - Fetch only needed columns
// - Compute KPIs client-side (cost per goods unit, avg cycle time, utilization)
function ERPDashboard({ onBack }) {
  // Log dashboard navigation
  useEffect(() => {
    logger.route("GET", "/erp-analytics", 200, { dashboard: "ERPDashboard" });
    logger.info("ERPDashboard mounted", { dashboard: "ERPDashboard" });
  }, []);

  const [rows, setRows] = useState([]);
  const [dataLoading, setDataLoading] = useState(true);
  const [dataError, setDataError] = useState(null);
  // Missing columns (non-critical, data still loads)
  const [missingColumns, setMissingColumns] = useState([]);

  const [selectedYear, setSelectedYear] = useState("All");
  const [selectedRegion, setSelectedRegion] = useState("All");
  const [selectedLocation, setSelectedLocation] = useState("All");

  // Load ERP data using centralized data service
  useEffect(() => {
    const loadData = async () => {
      setDataLoading(true);
      setDataError(null);
      setMissingColumns([]);

      try {
        const { data, missingColumns: missing } = await fetchERPData();
        setRows(data);
        setMissingColumns(missing || []);
      } catch (err) {
        setDataError(err.message ?? "Failed to load data");
      } finally {
        setDataLoading(false);
      }
    };

    loadData();
  }, []);

  // Process data using centralized processing logic
  const processedData = useMemo(
    () =>
      processERPData({
        rows,
        selectedYear,
        selectedRegion,
        selectedLocation,
        dataLoading,
        dataError,
        missingColumns,
      }),
    [rows, selectedYear, selectedRegion, selectedLocation, dataLoading, dataError, missingColumns]
  );

  // Handler functions for filter changes
  const handleYearChange = (newValue) => {
    setSelectedYear(newValue);
  };

  const handleRegionChange = (newValue) => {
    setSelectedRegion(newValue);
    setSelectedLocation("All"); // Reset location when region changes
  };

  const handleLocationChange = (newValue) => {
    setSelectedLocation(newValue);
  };

  const handleResetFilters = () => {
    setSelectedYear("All");
    setSelectedRegion("All");
    setSelectedLocation("All");
  };

  return (
    <section className="max-w-7xl mx-auto mt-10">
      <div className="rounded-3xl border border-emerald-500/40 bg-gradient-to-b from-emerald-900 via-emerald-800 to-emerald-900 px-5 py-6 shadow-emerald-900/40 shadow-2xl md:px-8 md:py-8">
        {/* Top bar with back button + breadcrumb */}
        <div className="flex items-center justify-between gap-4">
          <button
            onClick={() => {
              logger.route("GET", "/", 200, { from: "ERPDashboard", action: "back" });
              logger.info("Navigating back to main dashboard", { from: "ERPDashboard" });
              onBack();
            }}
            className="inline-flex items-center gap-2 text-sm text-emerald-100 hover:text-emerald-50
                       border border-emerald-400/40 rounded-full px-3 py-1 bg-emerald-900/40
                       hover:bg-emerald-800/60 transition"
          >
            <span className="text-lg leading-none"></span>
            <span>Back to dashboard</span>
          </button>

          <div className="text-xs text-emerald-100/70">
            Home / ERP Analytics
          </div>
        </div>

        {/* Module header */}
        <div className="mt-6">
          <p className="inline-flex items-center gap-2 rounded-full bg-emerald-900/60 border border-emerald-400/40 px-4 py-1 text-xs text-emerald-100">
            <span className="inline-block w-2 h-2 rounded-full bg-emerald-400" />
            ERP Module
          </p>
          <h2 className="mt-3 text-2xl md:text-3xl font-bold text-white tracking-tight">
            Supply-to-Stock KPIs: Cost, Cycle Time, Utilization
          </h2>
          <p className="mt-2 text-sm text-emerald-100/80 max-w-2xl">
            Track procurement spend to good units, end-to-end cycle speed, and material utilization across regions and locations.
          </p>
        </div>

        {/* Error message if data failed to load (schema/table missing) */}
        {dataError && (
          <p className="mt-4 text-xs text-red-200">
            Failed to load data: {dataError}
          </p>
        )}

        {/* Warning message if columns are missing (non-critical) */}
        {!dataError && missingColumns.length > 0 && (
          <p className="mt-4 text-xs text-yellow-200">
            Warning: Column(s) '{missingColumns.join("', '")}' do not exist in table 'erp_funnel_view' (schema 'erp'). 
            Some metrics may show "". Please check your database schema.
          </p>
        )}

        {/* Filters and KPI Cards */}
        <ERPFilters
          selectedYear={selectedYear}
          selectedRegion={selectedRegion}
          selectedLocation={selectedLocation}
          yearOptions={processedData.yearOptions}
          regionOptions={processedData.regionOptions}
          locationOptions={processedData.locationOptions}
          filteredCount={processedData.filteredCount}
          totalLoadedCount={processedData.totalLoadedCount}
          onYearChange={handleYearChange}
          onRegionChange={handleRegionChange}
          onLocationChange={handleLocationChange}
          onResetFilters={handleResetFilters}
        />

        <KPICards
          kpiCards={processedData.kpiCards}
          dataLoading={dataLoading}
          dataError={dataError}
        />

        {/* Charts and Visualizations */}
        <VendorQualityYieldChart
          supplierScatter={processedData.supplierScatter}
          dataLoading={dataLoading}
          dataError={dataError}
        />

        <OnTimeFulfillmentHeatmap
          onTimeHeatmap={processedData.onTimeHeatmap}
          heatmapYears={processedData.heatmapYears}
          heatmapRegions={processedData.heatmapRegions}
          dataLoading={dataLoading}
          dataError={dataError}
        />

        <CostBreakdownChart
          costBreakdown={processedData.costBreakdown}
          dataLoading={dataLoading}
          dataError={dataError}
        />

        <CycleTimeTrendChart
          cycleTrend={processedData.cycleTrend}
          dataLoading={dataLoading}
          dataError={dataError}
        />
      </div>
    </section>
  );
}

export default ERPDashboard;