import React from "react";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
} from "recharts";
import {
  ComposableMap,
  Geographies,
  Geography,
  Marker,
  ZoomableGroup,
} from "react-simple-maps";

const WORLD_GEO_URL =
  "https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json";

/**
 * Process Trend by PO Line Chart
 */
export function ProcessTrendChart({ chartData, dataLoading, dataError }) {
  return (
    <div className="mt-8 bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
      <div className="flex items-center justify-between flex-wrap gap-3 mb-4">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-sm font-semibold text-white">
              Process Trend by PO
            </h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Tracks four key metrics over time: Total Cost (blue), Lead Time in days (purple), Defect Quantity (orange), and Overall Equipment Effectiveness percentage (green). Shows how these metrics change across purchase orders to identify trends and patterns.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/70">
            PO-level lines for cost, lead time, defects, and OEE.
          </p>
        </div>
      </div>
      {dataLoading ? (
        <div className="text-xs text-emerald-100/70">Loading</div>
      ) : dataError ? (
        <div className="text-xs text-red-200">
          Failed to load data: {dataError}
        </div>
      ) : !chartData.length ? (
        <div className="text-xs text-emerald-100/70">
          No data for this selection.
        </div>
      ) : (
        <div className="w-full" style={{ height: 450 }}>
          <ResponsiveContainer width="100%" height="100%">
            <LineChart
              data={chartData}
              margin={{ top: 10, right: 100, left: 60, bottom: 90 }}
            >
              <CartesianGrid
                strokeDasharray="3 3"
                stroke="rgba(16,185,129,0.18)"
              />
              <XAxis
                dataKey="po_creation_date"
                tick={{ fontSize: 11, fill: "#A7F3D0" }}
                angle={-20}
                height={55}
                label={{
                  value: "Purchase order date",
                  position: "insideBottom",
                  offset: -5,
                  fill: "#A7F3D0",
                  fontSize: 11,
                  style: { textAnchor: "middle" },
                }}
              />
              {/* Left axis for total cost */}
              <YAxis
                yAxisId="left"
                tick={{ fontSize: 11, fill: "#A7F3D0" }}
                tickFormatter={(v) => Number(v).toLocaleString()}
                width={60}
                label={{
                  value: "Total Cost ($)",
                  angle: -90,
                  position: "left",
                  offset: 10,
                  fill: "#A7F3D0",
                  fontSize: 11,
                  style: { textAnchor: "middle" },
                }}
              />
              {/* Right axis for lead time, defect qty, and OEE (%) */}
              <YAxis
                yAxisId="right"
                orientation="right"
                tick={{ fontSize: 11, fill: "#A7F3D0" }}
                tickFormatter={(v) => Number(v).toFixed(0)}
                width={60}
                label={{
                  value: "Lead Time (days) / Defect Qty / OEE (%)",
                  angle: 90,
                  position: "right",
                  offset: 10,
                  fill: "#A7F3D0",
                  fontSize: 11,
                  style: { textAnchor: "middle" },
                }}
              />
              <Tooltip
                contentStyle={{
                  backgroundColor: "#022c22",
                  border: "1px solid rgba(16,185,129,0.4)",
                  borderRadius: "0.75rem",
                  fontSize: "11px",
                  color: "#ECFDF5",
                }}
                formatter={(value, name) => {
                  if (
                    value === null ||
                    value === undefined ||
                    Number.isNaN(value)
                  )
                    return "";
                  if (name === "oee_pct")
                    return [`${Number(value).toFixed(1)}%`, "OEE %"];
                  return [Number(value).toFixed(1), name];
                }}
                labelFormatter={(label, payload) => {
                  const po =
                    payload && payload.length
                      ? payload[0].payload.po_number
                      : "";
                  return `${label || "Unknown"}  PO ${po}`;
                }}
              />
              <Legend
                wrapperStyle={{ fontSize: 11, color: "#ECFDF5" }}
                verticalAlign="top"
                height={36}
              />
              <Line
                yAxisId="left"
                type="monotone"
                dataKey="total_cost"
                name="Total Cost"
                stroke="#22d3ee"
                strokeWidth={2}
                dot={{ r: 3 }}
              />
              <Line
                yAxisId="right"
                type="monotone"
                dataKey="lead_time_days"
                name="Lead Time (days)"
                stroke="#a855f7"
                strokeWidth={2}
                dot={{ r: 3 }}
              />
              <Line
                yAxisId="right"
                type="monotone"
                dataKey="defect_quantity"
                name="Defect Qty"
                stroke="#f97316"
                strokeWidth={2}
                dot={{ r: 3 }}
              />
              <Line
                yAxisId="right"
                type="monotone"
                dataKey="oee_pct"
                name="OEE %"
                stroke="#4ade80"
                strokeWidth={2}
                dot={{ r: 3 }}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
      )}
    </div>
  );
}

/**
 * Bubble Supply Map Component
 * Geographic visualization of customer, supplier, and plant locations
 */
export function BubbleSupplyMap({ nodes, filters }) {
  const plottedNodes = Array.isArray(nodes)
    ? nodes
        .filter((n) => n.coords)
        // Sort so suppliers render last (on top) to ensure visibility
        .sort((a, b) => {
          const roleOrder = { customer: 0, plant: 1, supplier: 2 };
          return (roleOrder[a.role] || 0) - (roleOrder[b.role] || 0);
        })
    : [];
  const hasData = plottedNodes.length > 0 && filters.customer;
  if (!hasData) {
    return (
      <div className="mt-8 bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
        <h3 className="text-sm font-semibold text-white">Location Footprint</h3>
        <p className="mt-1 text-xs text-emerald-100/70">
          Select a customer (and optionally part / vendor) to see the location
          footprint.
        </p>
        <div className="mt-3 text-xs text-emerald-100/70">
          No locations for this selection.
        </div>
      </div>
    );
  }
  // Auto-center & zoom based on node coordinates
  let center = [20, 20];
  let zoom = 1.3;
  if (plottedNodes.length > 0) {
    const lons = plottedNodes.map((n) => n.coords[0]);
    const lats = plottedNodes.map((n) => n.coords[1]);
    const minLon = Math.min(...lons);
    const maxLon = Math.max(...lons);
    const minLat = Math.min(...lats);
    const maxLat = Math.max(...lats);
    const spanLon = Math.abs(maxLon - minLon);
    const spanLat = Math.abs(maxLat - minLat);
    const span = Math.max(spanLon, spanLat);
    center = [(minLon + maxLon) / 2 || 20, (minLat + maxLat) / 2 || 20];
    if (span > 120) zoom = 0.9;
    else if (span > 60) zoom = 1.2;
    else if (span > 30) zoom = 1.6;
    else zoom = 2.0;
  }
  const bubbleRadius = () => {
    // Constant size for all bubbles
    return 10;
  };
  const roleFill = (role) => {
    if (role === "customer") return "#22c55e"; // emerald
    if (role === "supplier") return "#f97316"; // orange
    if (role === "plant") return "#38bdf8"; // blue
    return "#6b7280";
  };
  // Circular arrangement: place bubbles at 120 angles around the center point
  // Increased radius to prevent overlap when multiple bubbles share the same location
  const offsetCoordsForRole = (node) => {
    const [lon, lat] = node.coords;
    const radius = 2.8; // degrees - increased separation distance from center
    
    // Convert degrees to radians for calculations
    const toRad = (deg) => (deg * Math.PI) / 180;
    
    let angleDeg = 0; // Default (north)
    if (node.role === "customer") {
      angleDeg = 0; // North (0)
    } else if (node.role === "supplier") {
      angleDeg = 240; // Southwest (240)
    } else if (node.role === "plant") {
      angleDeg = 120; // Northeast (120)
    }
    
    const angleRad = toRad(angleDeg);
    // Calculate offset: longitude changes with cosine, latitude with sine
    // (adjusted for Mercator projection where longitude scales with latitude)
    const latAdjust = Math.cos(toRad(lat)); // Account for latitude compression
    const offsetLon = lon + (radius * Math.sin(angleRad)) / latAdjust;
    const offsetLat = lat + radius * Math.cos(angleRad);
    
    return [offsetLon, offsetLat];
  };
  const tooltipTitle = (node) => {
    const headerPrefix =
      node.role === "customer"
        ? "Customer"
        : node.role === "supplier"
        ? "Supplier"
        : "Plant";

    const regionStr = node.region ? `Region: ${node.region}` : "Region: ";
    const locationStr = node.location ? `Location: ${node.location}` : "Location: ";
    const header = `${headerPrefix}: ${node.name}\n${regionStr}, ${locationStr}`;

    // Show delayed days when on-time delivery is 0%, otherwise just show percentage
    let onTimeLine;
    if (
      node.on_time_delivery_pct != null &&
      !Number.isNaN(node.on_time_delivery_pct) &&
      node.on_time_delivery_pct === 0 &&
      node.avg_delayed_days != null &&
      !Number.isNaN(node.avg_delayed_days)
    ) {
      onTimeLine = `On-time delivery: 0.0% (Delayed: ${node.avg_delayed_days.toFixed(1)} days)`;
    } else if (
      node.on_time_delivery_pct != null &&
      !Number.isNaN(node.on_time_delivery_pct)
    ) {
      onTimeLine = `On-time delivery: ${node.on_time_delivery_pct.toFixed(1)}%`;
    } else {
      onTimeLine = `On-time delivery: `;
    }

    const leadTimeLine = `Avg lead time: ${
      node.avg_lead_time_days != null && !Number.isNaN(node.avg_lead_time_days)
        ? `${node.avg_lead_time_days.toFixed(1)} days`
        : ""
    }`;

    const costLine = `Total cost: $${Number(
      node.total_cost_sum || 0
    ).toLocaleString()}`;

    return [
      header,
      onTimeLine,
      leadTimeLine,
      costLine,
    ].join("\n");
  };
  return (
    <div className="mt-8 bg-[#0b1210]/70 border border-emerald-500/40 rounded-2xl p-6 shadow-lg backdrop-blur-sm">
      <div className="flex items-center justify-between flex-wrap gap-3 mb-4">
        <div className="flex-1">
          <div className="flex items-center gap-2">
            <h3 className="text-sm font-semibold text-white">Location Footprint</h3>
            <div className="group relative">
              <i className="fas fa-info-circle text-emerald-400/60 hover:text-emerald-300 cursor-help text-xs"></i>
              <div className="absolute bottom-full left-0 mb-2 w-56 bg-[#0b1210] border border-emerald-500/40 rounded-lg p-3 text-xs text-emerald-100 shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                Geographic map showing the physical locations of customers (green), suppliers (orange), and plants (blue) in your supply chain. Hover over bubbles to see performance metrics. Helps visualize your global supply network and identify optimization opportunities.
                <div className="absolute top-full left-4 -mt-1 border-4 border-transparent border-t-emerald-500/40"></div>
              </div>
            </div>
          </div>
          <p className="mt-1 text-xs text-emerald-100/70">
            Visualize the geographic distribution of your supply chain network. Identify customer, supplier, and plant locations to optimize logistics and reduce lead times.
          </p>
        </div>
      </div>
      {/* Zoomable bubble map */}
      <div className="w-full" style={{ height: 420 }}>
        <ComposableMap
          projection="geoMercator"
          projectionConfig={{ scale: 150 }}
          style={{ width: "100%", height: "100%" }}
        >
          <ZoomableGroup center={center} zoom={zoom} minZoom={0.7} maxZoom={4}>
            <Geographies geography={WORLD_GEO_URL}>
              {({ geographies }) =>
                geographies.map((geo) => (
                  <Geography
                    key={geo.rsmKey}
                    geography={geo}
                    fill="#065f46"
                    stroke="#047857"
                    strokeWidth={0.4}
                  />
                ))
              }
            </Geographies>
            {/* Bubbles per physical location (circular arrangement to avoid overlap) */}
            {plottedNodes.map((node) => (
              <Marker key={node.id} coordinates={offsetCoordsForRole(node)}>
                <title>{tooltipTitle(node)}</title>
                <circle
                  r={bubbleRadius()}
                  fill={roleFill(node.role)}
                  opacity={0.9}
                />
              </Marker>
            ))}
          </ZoomableGroup>
        </ComposableMap>
      </div>
      {/* Legend below map */}
      <div className="mt-4 grid gap-3 md:grid-cols-2 text-xs text-emerald-100/80">
        <div>
          <div className="font-semibold text-emerald-200 mb-1">Legend</div>
          <p className="flex items-center gap-2">
            <span className="inline-block w-2 h-2 rounded-full bg-emerald-400" />
            <span>Green circle = Customer location</span>
          </p>
          <p className="flex items-center gap-2">
            <span className="inline-block w-2 h-2 rounded-full bg-orange-400" />
            <span>Orange circle = Supplier location</span>
          </p>
          <p className="flex items-center gap-2">
            <span className="inline-block w-2 h-2 rounded-full bg-sky-400" />
            <span>Blue circle = Plant location</span>
          </p>
        </div>
        <div>
          <div className="font-semibold text-emerald-200 mb-1">
            How to read
          </div>
          <p>Each bubble represents a customer, supplier, or plant location.</p>
          <p>Hover a bubble to see on-time delivery, lead time, and total cost.</p>
        </div>
      </div>
    </div>
  );
}



